import { Component, ElementRef, EventEmitter, Input, OnInit, Output } from "@angular/core";
import {Router} from "@angular/router";
import {CommonService} from "../../services/common.service";
import $ from 'jquery';
@Component({
  selector: "app-price-widget",
  templateUrl: "./price-widget.component.html",
  styleUrls: ["./price-widget.component.scss"],
})
export class PriceWidgetComponent implements OnInit {
  @Input("imgURL") public imgURL: any;
  @Input("name") public name: string;
  @Input("bgColor") public bgColor: string;
  @Input("price") public price: number;
  @Input("dwelling") public dwelling: string;
  @Input("currpage") public currpage: string;
  @Input("pricesType") public pricesType: any;
  @Output("openModal") public openPriceModal: EventEmitter<any> = new EventEmitter<any>();
  homeSlider:object = {};

  constructor(public commonService: CommonService,public router: Router) {
    this.initOwlElement();
  }

  ngOnInit() {
    // console.log("asd: ", this.dwelling, typeof this.dwelling);
  }
  ngAfterViewInit(){
    $('.fa-chevron-left').on('click',(e)=>{
      console.log('object',e)
    })
  }

  openModal() {
    this.openPriceModal.emit();
  }


  initOwlElement(){
    this.homeSlider = {
      items:1,
      navText : ["<i class='fa fa-chevron-left'></i>","<i class='fa fa-chevron-right'></i>"],
      nav: true,
      autoplay: false,
      autoplayTimeout: 10000,
      loop: true,
      dots: true,
      callbacks:true,
      onDragged:(event)=>{
        console.log(event)
      },
      onChanged:(prop)=>{
        console.log(prop.currentTarget.children[0].children[0].children[prop.page.index+2])
        console.log(prop.currentTarget.children[0].children[0].children)
        console.log(prop.currentTarget)
      }
    };
  }
}
